<template>
  <window :title="isEditing ? '–ò–∑–º–µ–Ω–∏—Ç—å –∑–∞–ø–∏—Å—å' : '–î–æ–±–∞–≤–∏—Ç—å –∑–∞–ø–∏—Å—å'">
    <form>
      <ul>
        <li>
          <label for="name">–ù–∞–∏–º–µ–Ω–æ–≤–∞–Ω–∏–µ: </label>
          <div class="autocomplete">
            <input
              type="text"
              name="name"
              placeholder="–ù–∞–ø—Ä–∏–º–µ—Ä, –®—Ç—É–∫–∞—Ç—É—Ä–∫–∞"
              required
              autocomplete="off"
              v-model="name"
              @keydown="menuSelect"
            />
            <span v-if="acFiltered.length > 0 && !acShown">
              <p
                v-for="el in acFiltered"
                :class="acFiltered[selectedACID] === el ? 'selected' : null"
                :key="el"
                @click="selectAC(el)"
              >
                {{ el }}
              </p>
            </span>
          </div>
        </li>
        <li>
          <input type="checkbox" name="customCost" v-model="customCost" />
          <label for="customCost">–ù–µ—Å—Ç–∞–Ω–¥–∞—Ä—Ç–Ω—ã–π –ø—Ä–µ–¥–º–µ—Ç</label>
        </li>
        <li v-if="!customCost">
          <label for="area">–ö–æ–ª-–≤–æ + –ï–¥. –ò–∑–º–µ—Ä–µ–Ω–∏—è: </label>
          <input
            type="text"
            name="count"
            placeholder="–ù–∞–ø—Ä–∏–º–µ—Ä, 2.5 –º2"
            :value="
              isEditing ? `${editingData.count} ${editingData.units}` : null
            "
            required
          />
        </li>
        <li v-if="!customCost">
          <label for="costPMSQ">–¶–µ–Ω–∞ –Ω–∞ –µ–¥. –∏–∑–º.: </label>
          <input
            type="text"
            name="costPU"
            placeholder="–ù–∞–ø—Ä–∏–º–µ—Ä, 10"
            :value="isEditing ? editingData.costPU : null"
            required
          />
        </li>
        <li v-if="customCost">
          <label for="cost">–¶–µ–Ω–∞: </label>
          <input
            type="text"
            name="cost"
            placeholder="–ù–∞–ø—Ä–∏–º–µ—Ä, 75"
            :value="isEditing ? editingData.cost : null"
            required
          />
        </li>
        <li class="controls">
          <input
            type="button"
            :value="isEditing ? '–ò–∑–º–µ–Ω–∏—Ç—å' : '–î–æ–±–∞–≤–∏—Ç—å'"
            @click.prevent="parse()"
          />
          <input type="button" value="–û—Ç–º–µ–Ω–∞" @click.prevent="cancelCallback" />
        </li>
      </ul>
    </form>
  </window>
</template>

<script>
import Window from "./Window.vue";
import { ref } from "vue";
import acWords from "./acWords.js";

export default {
  components: { Window },
  name: "AddWindow",
  props: {
    addCallback: Function,
    isEditing: Boolean,
    editingData: Object,
    editCallback: Function,
    cancelCallback: Function,
  },
  data: () => ({
    acFiltered: [],
    acSelected: false,
    selectedACID: -1,
    acShown: true,
  }),
  setup(props) {
    var name = ref("");
    var customCost = ref(false);
    if (props.editingData !== null) {
      if (props.editingData.cost) customCost.value = true;
      if (props.editingData.name)
        name = JSON.parse(JSON.stringify(props.editingData.name));
    }

    function parse() {
      var name = document.getElementsByName("name")[0].value;
      if (!customCost.value) {
        var count = document.getElementsByName("count")[0].value;
        var costPU = parseFloat(document.getElementsByName("costPU")[0].value);
      } else {
        var cost = parseFloat(document.getElementsByName("cost")[0].value);
      }

      var proceed = true;
      var inputsToFill = [];

      if (name.length === 0) {
        proceed = false;
        inputsToFill.push("–ù–∞–∏–º–µ–Ω–æ–≤–∞–Ω–∏–µ");
      }

      if (customCost.value) {
        if (isNaN(cost)) {
          proceed = false;
          inputsToFill.push("–¶–µ–Ω–∞");
        }
      } else {
        var split = count.split(" ");
        if (isNaN(parseFloat(split[0]))) {
          proceed = false;
          inputsToFill.push("–ö–æ–ª-–≤–æ + –ï–¥. –ò–∑–º–µ—Ä–µ–Ω–∏—è");
        }
        if (isNaN(costPU)) {
          proceed = false;
          inputsToFill.push("–¶–µ–Ω–∞ –Ω–∞ –µ–¥. –∏–∑–º.");
        }
      }

      if (proceed) {
        var resultObj;
        if (customCost.value) {
          resultObj = {
            name,
            cost,
          };
        } else {
          resultObj = {
            name,
            count: parseFloat(split[0]),
            units: split[1],
            costPU,
          };
        }
        if (props.isEditing)
          props.editCallback(props.editingData.name, resultObj);
        else props.addCallback(resultObj);
      } else {
        alert(
          `–ü—Ä–æ–≤–µ—Ä—å—Ç–µ –Ω–∞–ø–∏—Å–∞–Ω–∏–µ –ø–æ–ª${
            inputsToFill.length === 1 ? "—è" : "–µ–π"
          } ${inputsToFill.join(", ")}.`
        );
      }
    }

    return { customCost, parse, name };
  },

  mounted() {
    document.querySelectorAll("form input").forEach((val) => {
      val.addEventListener("keydown", (e) => {
        if (e.key === "Enter") {
          e.preventDefault();
          // this.parse();
        }
      });
    });
  },

  watch: {
    name() {
      this.autoComplete();
    },
  },

  methods: {
    autoComplete() {
      if (this.name.length > 0) {
        let convertedName = this.name[0].toUpperCase() + this.name.toLowerCase().slice(1);
        this.acFiltered = [
          ...new Set([...acWords.filter((val) => {
              if (val.includes(convertedName)) return true;
              return false;
            }), ...acWords.filter((val) => {
              if (val.includes(this.name.toLowerCase())) return true;
              return false;
            })])
        ];
        
        if (this.acFiltered.length > 0) this.selectedACID = 0;
        else this.selectedACID = -1;
      } else {
        this.acFiltered = [];
        this.selectedACID = -1;
      }
    },

    selectAC(newName) {
      this.name = newName;
      this.acFiltered = [];
      this.selectedACID = -1;
      document.getElementsByName("name")[0].focus();
      this.acShown = true;
    },

    menuSelect(e) {
      this.acShown = false;
      switch (e.key) {
        case "ArrowDown":
          e.preventDefault();
          this.selectedACID++;
          if (this.selectedACID === this.acFiltered.length)
            this.selectedACID = 0;
          break;
        case "ArrowUp":
          e.preventDefault();
          this.selectedACID--;
          if (this.selectedACID < 0)
            this.selectedACID = this.acFiltered.length - 1;
          break;
        case "Enter":
          if (this.selectedACID !== -1) {
            e.preventDefault();
            this.selectAC(this.acFiltered[this.selectedACID]);
          }
          break;
      }
    },
  },
};
</script>

